<template>
	<div class="sub-container">
		<h2 class="board-title">{{ board.title }}</h2>
		<div class="board-container">
			<div class="board-header">
				<div class="board-info">
					<span class="type">작성일</span>
					<span class="data"
						>{{ $moment(board.createDate).format('YYYY.MM.DD') }}
					</span>
				</div>
				<div class="board-info">
					<span class="type">작성자</span>
					<span class="data">{{ writer.name }} </span>
				</div>
			</div>
			<div class="board-content">
				<div class="board-img">
					<img :src="board.imgPath" />
				</div>

				<div class="content" v-html="board.contents"></div>

				<div class="writer-box">
					<div class="writer marB-15">
						<span class="body3">{{ writer.name }}</span>
						<span class="gray">{{ writer.job }}</span>
					</div>
					<p>
						{{ writer.contents }}
					</p>
				</div>
				<LoadingSpinner :loading="isLoading" />
			</div>
		</div>
	</div>
</template>

<script>
import { fetchColumnDetail } from '@/api/column';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner },
	created() {
		let id = this.$route.params.id;
		this.fetchColumnDetail(id);
	},
	data() {
		return {
			isLoading: false,
			board: {},
			writer: {},
		};
	},
	methods: {
		async fetchColumnDetail(id) {
			this.isLoading = true;
			try {
				const { data } = await fetchColumnDetail(id);
				this.board = data;
				this.writer = data.columnWriter;
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
};
</script>

<style>
.writer-box {
	padding: 30px;
	box-sizing: border-box;
	background: #fafafa;
}

.writer span {
	margin-right: 5px;
}
</style>
